@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.elelment {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.element-main .logo {
    width: 11em;
    align-self: center;
}

.element-main {
    width: 32%;
    background: var(--color-background);
    margin: 2em auto 2em;
    border-radius: 5px;
    padding: 3em 2em;
    display: flex;
    flex-direction: column;
}

.element-main h1 {
    text-align: center;
    font-size: 2.3em;
    color: var(--bh-darkpurple);
    font-weight: 700;
    margin-top: 10px;
}

.element-main p {
    font-size: 1em;
    color: var(--grayScale-TWO);
    line-height: 1.5em;
    margin: 0.8em 0em;
    text-align: center;
}

.element-main input[type="password"] {
    font-size: 1em;
    color: var(--grayScale-TWO);
    background: var(--color-background);
    padding: 1em 0.5em;
    display: block;
    width: 95%;
    outline: none;
    margin: 0 auto;
    margin-bottom: 1em;
    text-align: center;
    border: 1px solid var(--grayScale-THREE);
}

.element-main input[type="submit"] {
    font-size: 1em;
    color: var(--grayScale-FIVE);
    background: var(--bh-lightpurple);
    width: 55%;
    padding: 0.8em 0em;
    outline: none;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    border-bottom: 3px solid var(--bh-darkpurple);
    display: block;
    margin: 1.5em auto 0;
}

.element-main input[type="submit"]:hover {
    background: var(--bh-darkpurple);
    transition: 0.5s all;
    -webkit-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -o-transition: 0.5s all;
}

/*--media quiries start here--*/

@media(max-width:1280px) {
    .element-main {
        width: 38%;
    }
    .element-main h1 {
        font-size: 1.8em;
    }
}

@media(max-width:1024px) {
    .element-main {
        width: 47%;
    }
    .element-main .logo {
        width: 9em;
    }
    .element-main h1 {
        font-size: 1.8em;
    }
}

@media(max-width:768px) {
    .element-main {
        width: 60%;
    }
    .element-main .logo {
        width: 8em;
    }
    .element-main h1 {
        font-size: 1.6em;
    }
    .element-main p {
        font-size: 0.9em;
    }
    .element-main input[type="submit"] {
        width: 75%;
        font-size: 0.9em;
    }
    .element-main input[type="password"] {
        font-size: 0.9em;
        width: 75%;
    }
}

@media(max-width:480px) {
    .element-main {
        width: 80%;
        padding: 3em 1.5em;
    }
    .element-main .logo {
        width: 7em;
    }
    .element-main input[type="submit"] {
        font-size: 0.8em;
        width: 75%;
    }
    .element-main input[type="password"] {
        font-size: 0.8em;
        width: 75%;
    }
}

@media(max-width:320px) {
    .element-main h1 {
        font-size: 1.2em;
    }
    .element-main {
        width: 90%;
        margin: 1em auto 0em;
        padding: 1.5em 1em;
    }
    .element-main p {
        font-size: 0.7em;
    }
    .element-main input[type="submit"] {
        font-size: 0.9em;
        width: 75%;
    }
    .element-main input[type="password"] {
        font-size: 0.7em;
        padding: 0.8em 0.5em;
    }
}

/*--media quiries end here--*/
:root {
  --color-background: #F0F0F7;
  --default-danger: #ec053d;
  --default-success: #04b43c;
  --default-warn: #fccc04;
  --grayScale-ONE: #1a1a1a;
  --grayScale-TWO: #666;
  --grayScale-THREE: #b6b6b6;
  --grayScale-FOUR: #dfdfdf;
  --grayScale-FIVE: #f4f4f4;
  --bh-orange: #e5822b;
  --bh-lightpurple: #aa6d93;
  --bh-darkpurple: #974578;
  --bh-green: #b8db7c;
  --bh-whitesmoke: #ededed;
}
.App {
  background: #974578;
  background: var(--bh-darkpurple);
  min-height: 100vh;
}
